.tag {
    background-color: #26262B;
    border: 1px solid #515155;
    
    padding: 6px 12px;

    max-width: 180px;
    width: fit-content;

    font-weight: 400;
    font-size: 14px;
    line-height: 20px;

    color: white;
    
    border-radius: 8px;

    transition: all 100ms linear;

    user-select: none;

    &_selected_true {
        border-color: #00E5BD;
    }

    &_selectable_true:not(&_selected_true) {
        cursor: pointer;
    }
}
