.upload-modal {
   position: absolute;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);

   width: 524px;
   min-height: 540px;
   box-sizing: border-box;
   padding: 28px 16px 16px;

   @media (min-width:740px) {
      padding: 72px 32px 32px;
      min-height: 640px;
   }

   border-radius: 24px;

   max-width: 95%;

   &__wrapper {
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 1000;
      top: 0;
      left: 0;
      overflow: hidden;
   }

   &__fade {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      position: absolute;

      background: #090B0AB8;
      z-index: -1;
   }


   &__title {
      font-size: 30px;
      font-weight: 500;
      line-height: 30px;
      color: white;

      max-width: 275px;

      margin-bottom: 30px;

      @media (min-width:740px) {
         font-size: 32px;
         line-height: 32px;

         margin-bottom: 60px;
      }
   }

   &__accent-text {
      color: #00E5BD;
   }

   &__close {
      position: absolute;
      top: 16px;
      right: 16px;
      padding: 13px;

      background-color: #211D29;
      border-radius: 12px;

      cursor: pointer;

      transition: all 100ms linear;

      &:hover {
         opacity: 0.75;
      }
   }

   &__input {
      border-bottom: 2px solid #717174;
      margin-bottom: 56px;
      transition: all 150ms linear;

      &_error_true {
         border-color: rgba(255, 0, 0, 0.5);
      }
   }

   &__error {
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      color: rgba(255, 0, 0, 0.5);
      margin-bottom: 10px;
   }

   &__slider {
      margin-bottom: 20px;
   }

   &__slider-tags {
      margin-bottom: 20px;
   }

   &__loader {
      animation-duration: 2s;
      animation-name: loading;
      animation-iteration-count: infinite;

      margin-bottom: 50px;
   }

   @keyframes loading {
      from {
        rotate: 0deg;
      }
    
      to {
        rotate: 360deg;
      }
   }

   &__subtitle {
      font-size: 18px;
      font-weight: 500;
      line-height: 24px;

      color: #A1A0A2;
      margin-top: -30px;
      margin-bottom: 40px;
   }


   &__result {
      border: 1px dashed #A1A0A2;
      padding: 16px;
      border-radius: 16px;

      margin-bottom: 20px;
   }

   &__result-video {
      width: 200px;
      // height: 300px;
      flex-shrink: 0;
   }

   &__result-title {
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      color: white;

      margin-bottom: 4px;
   }

   &__result-subtitle {
      font-size: 12px;
      font-weight: 400;
      line-height: 16px;
      color: #A3A3A5;
   }
}
