.search-input {
   width: 100%;
   
   
   &__wrapper {
      position: relative;
   }

   .ant-input-affix-wrapper .search-input__input {
      color: white;
      font-weight: 400;
      font-size: 16px;
      line-height: 24px;
   }

   &__suggest {
      position: absolute;
      
      left: 0;
      right: 0;
      transform: translate(0, 16px);

      background-color: #25252C;
      border-radius: 16px;
      
      font-size: 14px;

      padding: 32px 24px;

      color: white;
   }

   &__suggest-item {
      padding: 8px 16px;
      margin: 0 -16px;
      border-radius: 8px;

      cursor: pointer;
      color: white;

      position: relative;

      &_type {
         &_time {
            color: #00E5BD;
         }
      }

      &:hover {
         background-color: #2E2E34;
         
      }
   }

   &__suggest-item-close {
      position: absolute;
      top: 50%;
      right: 0;

      padding: 10px;

      transform: translate(0, -50%);
   }
}
