.list-layout {
   padding: 20px;

   &__logo {
      display: flex;
      width: 80px;
      flex-shrink: 0;
      margin-right: 32px;
   }

   &__search {
      width: 100%;
   }

   &__page {
      padding: 0 10px;

      @media (min-width:1000px) {
         padding: 0 120px;
      }
   }

   &__page-title {
      font-weight: 600;
      font-size: 48px;
      line-height: 52px;

      color: white;

      margin-bottom: 12px;

      @media (min-width:1000px) {
         margin-bottom: 52px;
      }
   }

   &__items {
      margin-bottom: 20px;
   }

   &__item {
      width: 100%;
      height: 500px;

      box-sizing: border-box;
   }

   &__filters-row {
      z-index: 1;
      padding: 32px 32px 0;
      margin: 0 -32px 32px;
      overflow: auto;

      position: relative;

      &_opened_true {
         background-color: #25252C;
         border-radius: 32px 32px 0 0;
      }
   }

   &__filters-row-content {
      position: absolute;
      left: 0;
      width: 100%;
      top: 100%;

      padding: 26px 32px 48px 32px;

      background-color: #25252C;
      border-radius: 0 0 32px 32px;

      box-sizing: border-box;

      font-size: 16px;
      font-weight: 400;
      line-height: 24px;

      color: white;
   }

   &__filters-row-content-item {
      cursor: pointer;
      width: fit-content;

      transition: all 100ms linear;

      &:hover {
         opacity: 0.75;
      }
   }
}
