.button {
    display: flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    gap: 10px;

    box-sizing: border-box;

    font-size: 16px;
    font-weight: 600;
    line-height: 24px;

    border-radius: 16px;
    padding: 12px 16px;

    user-select: none;
    cursor: pointer;

    &_theme {
        &_base {
            background-color: #00E2B8;
            color: #012B24;

            &:hover {
                opacity: 0.75;
            }
        }

        &_fade {
            color: #A3A3A5;
            border: 1px solid #767679;

            &:hover {
                background: rgba(228, 228, 228, 0.05);
            }
        }

        &_inverse {
            background-color: #182F2F;
            color: #00E2B8;

            &:hover {
                background: rgba(0, 229, 188, 0.05);
            }
        }
    }

    &_width {
        &_full {
            width: 100%;
        }
    }

    transition: all 200ms linear;
}
