.video {
    width: 100%;
    height: 100%;
    border-radius: 16px;
    object-fit: cover;
    

    &__wrapper {
        width: 100%;
        height: 100%;
        position: relative;
    }

    &__highlight {
        position: absolute;
        top: 10px;
        left: 50%;
        transform: translate(-50%, 0);
        width: 80%;
        border-radius: 8px;

        padding: 6px 10px;

        background-color: rgba(0, 0, 0, 0.544);
        color: white;

        font-size: 14px;
        line-height: 18px;
    }

    &__search {
        color: #00E2B8;
    }
}
